"use client"

import { useState } from "react"
import Link from "next/link"
import { Header } from "@/components/layout/header"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"
import { Calendar } from "@/components/ui/calendar"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Pagination } from "@/components/documents/pagination"
import {
  Plus,
  Search,
  Calendar as CalendarIcon,
  ListFilter,
  EllipsisVertical,
  ArrowUpDown,
  ChevronsUpDown,
} from "lucide-react"
import { mockUsers } from "@/lib/mock-users"
import { cn } from "@/lib/utils"
import { format } from "date-fns"

export default function UsersPage() {
  const [searchQuery, setSearchQuery] = useState("")
  const [date, setDate] = useState<Date | undefined>(undefined)
  const [currentPage, setCurrentPage] = useState(1)
  const [pageSize, setPageSize] = useState(10)

  const filteredUsers = mockUsers.filter((user) => {
    const fullName = `${user.lastName} ${user.firstName} ${user.middleName}`
    const matchesSearch =
      fullName.toLowerCase().includes(searchQuery.toLowerCase()) ||
      user.email.toLowerCase().includes(searchQuery.toLowerCase())
    return matchesSearch
  })

  const totalPages = Math.ceil(filteredUsers.length / pageSize)
  const startIndex = (currentPage - 1) * pageSize
  const endIndex = startIndex + pageSize
  const paginatedUsers = filteredUsers.slice(startIndex, endIndex)

  const getStatusBadge = (status: string) => {
    if (status === "active") {
      return (
        <Badge className="bg-[#22C55E] text-white border-0 hover:bg-[#22C55E] text-xs font-semibold">
          Активный
        </Badge>
      )
    } else if (status === "inactive") {
      return (
        <Badge className="bg-[#DC2626] text-[#FEF2F2] border-0 hover:bg-[#DC2626] text-xs font-semibold">
          Неактивный
        </Badge>
      )
    }
    return null
  }

  const getInitials = (user: typeof mockUsers[0]) => {
    return `${user.lastName[0]}${user.firstName[0]}`
  }

  return (
    <div className="min-h-screen bg-gray-50/50">
      <Header />
      <div className="max-w-[1280px] mx-auto px-8 py-6">
        {/* Заголовок */}
        <div className="flex items-center justify-between mb-6 pb-3">
          <div className="flex items-center gap-3">
            <h1 className="text-xl font-semibold leading-none">Пользователи</h1>
            <Badge variant="outline" className="text-xs font-semibold px-2 py-0.5">
              68
            </Badge>
          </div>
          <Button size="sm" className="gap-2 h-9 px-3 text-xs font-medium">
            <Plus className="h-4 w-4" />
            Добавить пользователя
          </Button>
        </div>

        {/* Фильтры */}
        <div className="mb-6 pb-6">
          <div className="flex items-center gap-3">
            <div className="relative w-[300px]">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground pointer-events-none" />
              <Input
                placeholder="Поиск по ФИО или email"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-9 h-9 text-sm"
              />
            </div>
            <Popover>
              <PopoverTrigger asChild>
                <Button variant="outline" className="h-9 gap-2 px-4 text-sm font-normal">
                  <CalendarIcon className="h-4 w-4" />
                  <span className="text-sm">
                    {date ? format(date, "dd MMM yyyy") : "Дата регистрации"}
                  </span>
                </Button>
              </PopoverTrigger>
              <PopoverContent className="w-auto p-0" align="start">
                <Calendar
                  mode="single"
                  selected={date}
                  onSelect={setDate}
                  initialFocus
                />
              </PopoverContent>
            </Popover>
            <DropdownMenu>
              <DropdownMenuTrigger asChild>
                <Button variant="outline" className="h-9 gap-2 px-4 text-sm font-medium">
                  <ListFilter className="h-4 w-4" />
                  Фильтр
                </Button>
              </DropdownMenuTrigger>
              <DropdownMenuContent align="end">
                <DropdownMenuItem>Все роли</DropdownMenuItem>
                <DropdownMenuItem>Спортсмен</DropdownMenuItem>
                <DropdownMenuItem>Судья</DropdownMenuItem>
                <DropdownMenuItem>Спортивный менеджер</DropdownMenuItem>
              </DropdownMenuContent>
            </DropdownMenu>
          </div>
        </div>

        {/* Таблица */}
        <div className="bg-white border border-border rounded-lg overflow-hidden">
          <Table>
            <TableHeader>
              <TableRow className="border-b">
                <TableHead className="h-9 px-4 font-medium">
                  <Button variant="ghost" className="h-auto p-0 font-medium text-sm text-muted-foreground hover:text-foreground hover:bg-transparent">
                    Пользователь
                  </Button>
                </TableHead>
                <TableHead className="h-9 px-2 font-medium">
                  <Button variant="ghost" className="h-auto p-0 font-medium text-sm text-muted-foreground hover:text-foreground hover:bg-transparent gap-1">
                    Роль
                    <ChevronsUpDown className="h-4 w-4" />
                  </Button>
                </TableHead>
                <TableHead className="h-9 px-2 font-medium">
                  <Button variant="ghost" className="h-auto p-0 font-medium text-sm text-muted-foreground hover:text-foreground hover:bg-transparent">
                    Email
                  </Button>
                </TableHead>
                <TableHead className="h-9 px-2 font-medium">
                  <Button variant="ghost" className="h-auto p-0 font-medium text-sm text-muted-foreground hover:text-foreground hover:bg-transparent">
                    Телефон
                  </Button>
                </TableHead>
                <TableHead className="h-9 px-2 font-medium">
                  <Button variant="ghost" className="h-auto p-0 font-medium text-sm text-muted-foreground hover:text-foreground hover:bg-transparent gap-1">
                    Статус
                    <ChevronsUpDown className="h-4 w-4" />
                  </Button>
                </TableHead>
                <TableHead className="h-9 px-2 font-medium">
                  <Button variant="ghost" className="h-auto p-0 font-medium text-sm text-muted-foreground hover:text-foreground hover:bg-transparent gap-1">
                    Последний вход
                    <ChevronsUpDown className="h-4 w-4" />
                  </Button>
                </TableHead>
                <TableHead className="h-9 px-2 w-[60px]"></TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {paginatedUsers.map((user) => (
                <TableRow key={user.id} className="border-b">
                  <TableCell className="px-4 py-2">
                    <div className="flex items-center gap-2">
                      <Avatar className="h-8 w-8">
                        <AvatarImage src={user.avatar} />
                        <AvatarFallback className="text-sm bg-[#F5F5F5] text-foreground">
                          {getInitials(user)}
                        </AvatarFallback>
                      </Avatar>
                      <span className="text-sm">
                        {user.lastName} {user.firstName} {user.middleName}
                      </span>
                    </div>
                  </TableCell>
                  <TableCell className="px-2 py-2">
                    <span className="text-sm text-muted-foreground">{user.role}</span>
                  </TableCell>
                  <TableCell className="px-2 py-2">
                    <span className="text-sm text-muted-foreground">{user.email}</span>
                  </TableCell>
                  <TableCell className="px-2 py-2">
                    <span className="text-sm text-muted-foreground">{user.phone}</span>
                  </TableCell>
                  <TableCell className="px-2 py-2">
                    {getStatusBadge(user.status)}
                  </TableCell>
                  <TableCell className="px-2 py-2">
                    <span className="text-sm text-muted-foreground">{user.lastLogin}</span>
                  </TableCell>
                  <TableCell className="px-2 py-2">
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                        <Button variant="ghost" size="icon" className="h-8 w-8">
                          <EllipsisVertical className="h-4 w-4" />
                        </Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent align="end">
                        <DropdownMenuItem asChild>
                          <Link href={`/users/${user.id}`}>Просмотр</Link>
                        </DropdownMenuItem>
                        <DropdownMenuItem asChild>
                          <Link href={`/users/${user.id}/edit`}>Редактировать</Link>
                        </DropdownMenuItem>
                        <DropdownMenuItem variant="destructive">
                          Удалить
                        </DropdownMenuItem>
                      </DropdownMenuContent>
                    </DropdownMenu>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>

        {/* Пагинация */}
        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          pageSize={pageSize}
          onPageChange={setCurrentPage}
          onPageSizeChange={(size) => {
            setPageSize(size)
            setCurrentPage(1)
          }}
        />
      </div>
    </div>
  )
}
